import React,{useEffect, useState} from 'react'
import styled from '@emotion/styled'
import Contact from './Contact'
import "aos/dist/aos.css";
import AOS from 'aos';

function Footer() {

    const Footer = styled.div`
        background: #262A35;
        padding: 20px 0 0 0;
        .tag__container{
            display:flex;
            justify-content:center;
            .tag__contact{
                display:inline-block;
                &:after{
                    content: '';
                    display: block;
                    margin: 10px auto;
                    width: 80%;
                    height: 3px;
                    background: white;
                }
            }
        }
        h2{
            color:white;
            text-align:center;
            padding:60px 0 24px 0;
        }
        .copyright{
            color:white;
            padding:12px;
            text-align:center;
            background: #1B242F;
            margin-top:32px;
        }
    `

    useEffect(()=>{
        AOS.init({
            duration : 600
        });
    },[])

    return (
        <Footer>
            <div className="tag__container" id="contact" data-aos="fade-up">
                <h2 className="tag__contact">CONTACT</h2>
            </div>
            <Contact />
            <p className="copyright">Rian Mandala Putra &copy;{new Date().getFullYear()}</p>
        </Footer>
    )
}

export default Footer
